import React, {Component} from 'react';
import {scaleLinear} from 'd3-scale';
import {csv as d3Csv} from 'd3-request';
import './Timeline.css';
import timeline from './data/Timeline.csv';
import preferences from './preferences';

class Timeline extends Component {

    constructor(props) {
        super(props);

        this.chart = React.createRef();

        this.viewbox = this.viewbox.bind(this);
        this.setData = this.setData.bind(this);

        this.lineWidth = 10;
        this.dataPointsHeight = 90;
        this.data = [];

        this.state = {
            areas: [],
            chartHeight: { height: '1000px' },
            chunks: [],
            connectors:  [],
            lines: [],
            years: []
        };

        d3Csv(timeline, this.setData);
    }

    componentDidMount() {
        window.addEventListener('resize', this.viewbox, false);
        this.viewbox();
    }

    setData(error, data) {
        if (!error && data.length > 0) {
            // Riordino i dati per "linea" e "anno di inizio" cosi scrivo una colonna alla volta
            this.data = data.sort( (a,  b) => {
                if (parseInt(a.Line,10) < parseInt(b.Line,10)) return -1;
                if (parseInt(a.Line,10) > parseInt(b.Line,10)) return 1;
                if (parseInt(a.From,10) < parseInt(b.From,10)) return -1;
                return 1;
            } );
            this.viewbox();
        }
    }

    viewbox() {
        if (this.data.length > 0) {
            // Min and Max Years
            const minYear = this.data.reduce( (min,  value) => ((parseInt(value.From,10) < min) ? parseInt(value.From,10) :  min
            ), 9999) - 5;
            const maxYear = this.data.reduce( (max,  value) => ((parseInt(value.To,10) > max) ? parseInt(value.To,10) :  max), 0) + 5;

            // Line Width
            const lines = this.data.reduce( (max,  value) => ((parseInt(value.Line,10) > max) ? parseInt(value.Line,10) :  max), 0);
            const lineWidth = Math.max(Math.floor((this.chart.current.offsetWidth / lines)), this.lineWidth);

            // Years
            let rawYears = this.data.map( item => parseInt(item.From,10) );
            // Add events and elections
            rawYears = rawYears.concat(preferences.events.map( item => item.year ))
                .concat(preferences.elections.map( item => item.year ))
                .concat(preferences.areas.map( item => item.from ))
                .concat(preferences.areas.map( item => item.to ));
            rawYears.push(maxYear)
            rawYears.push(minYear);
            rawYears = rawYears.filter( (val, index) => rawYears.indexOf(val) === index )
                .sort();
            
            // Height of the container
            const height = (this.dataPointsHeight * rawYears.length);
            const chartHeight = { height: height + `px` };
            // Scale
            var scaleY = scaleLinear()
                .domain([ minYear, maxYear ])
                .range([0, height]);
            
            // Let's create the list for the calendsar
            const years = [];
            const yearsLines = [];
            rawYears.forEach(year => {
                const top =  Math.floor(scaleY(year)) + 'px';
                years.push({
                    label: year,
                    position: { top }
                });
                yearsLines.push({
                    position: { top }
                });
            });

            // Vertical lines
            const chunks = [];
            const connectors = [];

            this.data.forEach(item => {
                const top = Math.floor(scaleY(parseInt(item.From,10)));
                const bottom = Math.floor(scaleY(parseInt(item.To,10)));
                const left = (parseInt(item.Line,10) - 1) * lineWidth;
                chunks.push({
                    bottom,
                    className: item.Modifier,
                    color: { background: preferences.colors[(item.Line-1)] },
                    id: item.ID,
                    label: item.Name,
                    leader: item.Leader,
                    left,
                    linkFrom: item.LinkFrom,
                    linkTo: item.LinkTo,
                    logo: (item.Logo) ? { backgroundImage: `url(/assets/logos/${item.Logo}.png)`, borderColor: preferences.colors[(item.Line-1)] } : null,
                    style: {
                        height: `${bottom - top}px`,
                        left:  `${left}px`,
                        top: `${top}px`,
                        width: `${lineWidth}px`,
                    },
                    top,
                    yearFrom: item.From,
                    yearTo: item.To
                });
            });
            // Horizontal connectors
            // Li controllo sull'Array che ho appena geenrato cosi ho gia' tutte le misure
            chunks.forEach(item => {
                if (item.linkFrom && parseInt(item.linkFrom) > 0) {
                // From
                    let source = chunks.filter( filtered => filtered.id === item.linkFrom );
                    if (source && source.length > 0) {
                        const start = (source[0].left < item.left) ? source[0].color.background : item.color.background;
                        const stop = (source[0].left < item.left) ? item.color.background : source[0].color.background;
                        connectors.push({
                            background: `linear-gradient(to right, ${start} 0%, ${stop} 100%)`,
                            className: `${item.className} ${source[0].className}`,
                            left: `${Math.min(source[0].left, item.left)}px`,
                            width: `${Math.abs(source[0].left - item.left)}px`,
                            top: `${item.top}px`,
                            transform: `translate3d(${Math.round(lineWidth/2)}px,-1px,0)`,
                        });
                    }
                }
                // To
                if (item.linkTo && parseInt(item.linkTo) > 0) {
                    let source = chunks.filter( filtered => filtered.id === item.linkTo );
                    if (source && source.length > 0) {
                        const start = (source[0].left < item.left) ? source[0].color.background : item.color.background;
                        const stop = (source[0].left < item.left) ? item.color.background : source[0].color.background;
                        connectors.push({
                            background: `linear-gradient(to right, ${start} 0%, ${stop} 100%)`,
                            className: `${item.className} ${source[0].className}`,
                            left: `${Math.min(source[0].left, item.left)}px`,
                            width: `${Math.abs(source[0].left - item.left)}px`,
                            top: `${item.bottom}px`,
                            transform: `translate3d(${Math.round(lineWidth/2)}px,-1px,0)`,
                        });
                    }
                }
            });
            // Areas
            const areas = preferences.areas.map(
                (item) => {
                    const top = scaleY(item.from);
                    const bottom = scaleY(item.to);
                    return {
                        style: {
                            height: `${bottom-top}px`,
                            top: `${top}px`,
                        },
                        title: item.title
                    }
                }
            )

            this.setState( { areas, chartHeight, chunks, connectors, lines: yearsLines, years } );
        }
    }

    render() {
        return (
            <section className="Timeline" style={this.state.chartHeight}>
                {
                    this.state.areas.map(
                        (area,index) => {
                            return (
                                <div key={index} style={area.style} className='area'>
                                    { /* <div className="image" style={area.image.style} /> */ }
                                    <h2>{area.title}</h2>
                                </div>
                            )
                        }
                    )
                }
                {
                    this.state.lines.map(
                        (line, index) => {
                            if ( index !== 0 && index < this.state.lines.length - 1 )
                                return <div key={index} className="Line" style={line.position} />
                            return null;
                        }
                    )
                }
                <aside className="Time">
                    {
                        this.state.years.map(
                            (year, index) => {
                                if ( index !== 0 && index < this.state.years.length - 1 ) {
                                    return (
                                        <div key={year.label} style={year.position} className="year">
                                            <div className="label">{year.label}</div>
                                        </div>
                                    );
                                }
                                return null;
                            }
                        )
                    }
                </aside>
                <div className="Elections"></div>
                <div className="Chart" ref={this.chart}>
                    {
                        this.state.connectors.map(
                            (connector,index) => {
                                return (
                                    <div key={index} style={connector} className={`connector ${connector.className}`} />
                                )
                            }
                        )
                    }
                    {
                        this.state.chunks.map(
                            (chunk, index) => {
                                return (
                                    <div key={index} style={chunk.style} className={`chunk ${chunk.className}`} id={`P${chunk.id}`}>
                                        <div className="details">
                                            <div className="bullet-top" style={chunk.color} />
                                            <div className="bullet-bottom" style={chunk.color} />
                                            <div className="line" style={chunk.color} />
                                            {
                                                (() => {
                                                    if (chunk.label.length > 0) {
                                                        return <div className="label">{chunk.label} {chunk.id}</div>
                                                    }
                                                    return null;
                                                })()
                                            }
                                            {
                                                (() => {
                                                    if (chunk.logo) {
                                                        return <div className="logo" style={chunk.logo} />
                                                    }
                                                    return null;
                                                })()
                                            }
                                        </div>
                                    </div>
                                )
                            }
                        )
                    }
                </div>
            </section>
        );
    }
}

export default Timeline;